	methods: {
		card_search(){
			let isCanUse=uni.getStorageSync('isCanUse')
			if(isCanUse=="100"){
				if(this.search_val===""){
					this.getCardList()
					return false
				}
				let str=JSON.stringify(this.search_val)
				console.log(str)
